COSFN.prototype.cosUpload = function (params) {
    params = params || {
        hashName: '',
        file: null,
        SliceSize: 1024,
        TaskReady: function (taskId) {
        },
        onHashProgress: function (progressData) {
        },
        onProgress: function (progressData) {
        },
        complete: function (err, data) {
        }
    }
    let _this = this;
    if (!params.hashName || !params.file) {
        return this.$Message.error('');
    }
    this.showLoading();
    //BucketRegion
    const realUpload = (data) => {
        //Key
        const remoteKey = data.remotePath + params.hashName;
        this.cos.sliceUploadFile({
            Bucket: data.Bucket,
            Region: data.Region,
            Key: remoteKey, //data.remotePath + params.Key,
            Body: params.file,
            SliceSize: params.SliceSize > 0 ? params.SliceSize : 1024, //
            TaskReady: (taskId) => { /*  */
                _this.currentTaskId = taskId;
                //  cos.cancelTask(taskId);
                params.TaskReady && params.TaskReady(taskId);
            },
            onHashProgress: (progressData) => { /*  */
                //md5onProgress localStorage  cos_sdk_upload_cache 
                params.onHashProgress && params.onHashProgress(progressData)
            },
            onProgress: (progressData) => { /*  */
                //{"loaded":53362688,"total":323406506,"speed":1030730.52,"percent":0.17}
                params.onProgress && params.onProgress(progressData);
            }
        }, (err, data) => {
            this.hideLoading();
            if (data) {
                data.remoteKey = remoteKey;
            }
            params.complete && params.complete(err, data);
        });
    }
    if (this.options.Bucket && this.options.Region) {
        realUpload(this.options)
    } else
        this.getSign((data) => {
            this.options.downloadHostName = data.downloadHostName;
            this.options.picHostName = data.picHostName;
            this.options.tencentCloudHostName = data.tencentCloudHostName;
            this.options.remotePath = data.remotePath;
            this.options.Bucket = data.bucketName;
            this.options.Region = data.region;
            this.options.qcloudCosUrl = data.tencentCloudHostName;
            realUpload(this.options);
        })
}

COSFN.prototype.readBlob = (options) => {
    var data = options.data || {};
    var reader = new FileReader();
    reader.onabort = function (e) {
        if (options.abort) options.abort(e, data);
    };
    reader.onload = function (e) {
        if (options.load) options.load(e, data);
    };
    reader.onerror = function (e) {
        if (options.error) options.error(e, data);
    };
    reader.onloadstart = function (e) {
        if (options.loadstart) options.loadstart(e, data);
        if (options.start) options.start(e, data);
    };
    reader.onloadend = function (e) {
        if (options.loadend) options.loadend(e, data);
        if (options.end) options.end(e, data);
    };
    reader.onprogress = function (e) {
        if (options.progress) options.progress(e, data);
    };
    if (options.type === "url") reader.readAsDataURL(options.blob);
    else reader.readAsArrayBuffer(options.blob);
    return reader;
};
export default COSFN;



