//
const TRANS = {
    transSingleGuarantorPersonalToForm: transSingleGuarantorPersonalToForm,
    transSingleGuarantorEnterpriseToForm: transSingleGuarantorEnterpriseToForm,
    transToForm: (bs, data) => {
        var row = undefined;
        switch (bs) {
            case 'personal':
                row = transPersonalToForm(data);
                break;
            case 'enterprise':
                row = transEnterpriseToForm(data);
                break;
            case 'relative':
                row = transRelativeToForm(data);
                break;
            case 'contactor':
                row = transContactorToForm(data);
                break;
            case 'guarantorPersonal':
                row = transGuarantorPersonalToForm(data);
                break;
            case 'guarantorEnterprise':
                row = transGuarantorEnterpriseToForm(data);
                break;
        }
        return row;
    },
    transToSave: (bs, cust) => {
        var row = undefined;
        var data = cust[bs];
        switch (bs) {
            case 'personal':
                row = transPersonalToSave(data);
                break;
            case 'enterprise':
                row = transEnterpriseToSave(data);
                break;
            case 'relative':
                row = transRelativeToSave(data);
                break;
            case 'contactor':
                row = transContactorToSave(data);
                break;
            case 'guarantorPersonal':
                row = transGuarantorPersonalToSave(data);
                break;
            case 'guarantorEnterprise':
                row = transGuarantorEnterpriseToSave(data);
                break;
        }
        return row;
    }
}
export default TRANS;



