//
const mt = {
    //
    personalFieldsTotal: (cust) => {
        return 31;
    },
    //
    personalFieldsCount: (cust) => {
        let num = 0,
            fields = FIELDS.personalFields,
            obj = cust.personal || {};
        fields.forEach((key, i) => {
            if (obj[key] !== undefined && obj[key] !== "")
                num++;
        })
        return num;
    },
    //-
    relativeFieldsTotal: (cust) => {
        return 11;
    },
    //-
    relativeFieldsCount: (cust) => {
        let num = 0,
            fields = FIELDS.relativeFields,
            obj = cust.relative || {};
        fields.forEach((key, i) => {
            if (obj[key] !== undefined && obj[key] !== "")
                num++;
        })
        return num;
    },
    //
    enterpriseFieldsTotal: (cust) => {
        if (!cust.enterprise) return 0;
        let num = 33;
        if (cust.enterprise.orderApplicationType != '1') {//  12
            num++;
        }
        if (cust.enterprise.isLongCertificate != '1') { //  10
            num++;
        }
        return num;
    },
    //
    enterpriseFieldsCount: (cust) => {
        if (!cust.enterprise) return 0;
        let num = 0,
            fields = FIELDS.enterpriseFields,
            obj = cust.enterprise || {};
        fields.forEach((key, i) => {
            if (key == 'eigenCode') {
                if (obj.orderApplicationType != '1' && !!obj[key])
                    num++
            } else if (key == 'certificateExpiryDate') {
                if (obj.isLongCertificate != '1' && !!obj[key])
                    num++
            } else if (key == 'registeredCapital' || key == 'corporationShareholdingRatio') {
                if (stringToNumber(obj[key]) !== '')
                    num++
            } else if (obj[key] !== undefined && obj[key] !== "")
                num++;
        })
        return num;
    },
    //
    contactorFieldsTotal: (cust) => {
        if (!cust.contactor) return 0;
        return cust.contactor.length * 3;
    },
    //
    contactorFieldsCount: (cust) => {
        let num = 0,
            fields = FIELDS.contactorFields,
            arr = cust.contactor || [];
        arr.forEach((obj, i) => {
            fields.forEach((key) => {
                if (obj[key] !== undefined && obj[key] !== "")
                    num++;
            })
        })
        return num;
    },
    //   19 * 
    guarantorPersonalFieldsTotal: (cust) => {
        if (!cust.guarantorPersonal) return 0;
        return cust.guarantorPersonal.length * FIELDS.guarantorPersonalFields.length;
    },
    //
    guarantorPersonalFieldsCount: (cust) => {
        let num = 0,
            fields = FIELDS.guarantorPersonalFields,
            arr = cust.guarantorPersonal || [];
        arr.forEach((obj, i) => {
            fields.forEach((key) => {
                if (obj[key] !== undefined && obj[key] !== "")
                    num++;
            })
        })
        return num;
    },
    // 18 * 
    guarantorEnterpriseFieldsTotal: (cust) => {
        if (!cust.guarantorEnterprise) return 0;
        return cust.guarantorEnterprise.length * FIELDS.guarantorEnterpriseFields.length;
    },
    //
    guarantorEnterpriseFieldsCount: (cust) => {
        if (!cust.guarantorEnterprise) return 0;
        let num = 0,
            fields = FIELDS.guarantorEnterpriseFields,
            arr = cust.guarantorEnterprise || [];
        arr.forEach((obj, i) => {
            fields.forEach((key) => {
                if (obj[key] !== undefined && obj[key] !== "")
                    num++;
            })
        })
        return num;
    },
    //
    getFieldsCount: (cust, isQy) => {
        if (!cust) return 0;
        var basicFieldsCount = isQy ? mt.enterpriseFieldsCount(cust) : mt.personalFieldsCount(cust) + mt.relativeFieldsCount(cust);
        return basicFieldsCount + mt.contactorFieldsCount(cust) + mt.guarantorPersonalFieldsCount(cust) + mt.guarantorEnterpriseFieldsCount(cust);
    },
    //
    getFieldsTotal: (cust, isQy) => {
        if (!cust) return 0;
        var basicFieldsTotal = isQy ? mt.enterpriseFieldsTotal(cust) : mt.personalFieldsTotal(cust) + mt.relativeFieldsTotal(cust);
        return basicFieldsTotal + mt.contactorFieldsTotal(cust) + mt.guarantorPersonalFieldsTotal(cust) + mt.guarantorEnterpriseFieldsTotal(cust);
    },

}

//
export const getCustFieldNum = (cust, isQy) => {
    var obj = {total: 0, count: 0};
    if (!cust) return obj;
    obj.total = mt.getFieldsTotal(cust, isQy);
    obj.count = mt.getFieldsCount(cust, isQy);
    return obj;
}

export default {
    MODEL,
    FIELDS,
}


