<template>
    <div class="region-picker-con">
        <Form ref="formModel" :model="formModel" :rules="regionRule" :label-width="0"
              v-if="formModel">
            <Row class="font0" style1="max-width:540px;">
                <i-col :span="type==2?12:8" v-if="showProvince">
                    <Form-item prop="province">
                        <i-select type="text" v-model.sync="formModel.province"
                                  :clearable="clearable"
                                  @on-change="changeProvince"
                                  placeholder="">
                            <i-option v-for="(item,index) in dp['province']" :value="item.dictCode"
                                      :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </Form-item>
                </i-col>
                <i-col :span="type==2?12:8" v-if="showCity">
                    <Form-item prop="city">
                        <i-select type="text" v-model.sync="formModel.city" :clearable="clearable"
                                  :disabled="!formModel.province"
                                  @on-change="changeCity" placeholder="">
                            <i-option v-for="(item,index) in cityList" :value="item.dictCode" :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </Form-item>
                </i-col>
                <i-col :span="type==2?12:8" v-if="showDistrict">
                    <Form-item prop="district">
                        <i-select type="text" v-model.sync="formModel.district"
                                  :clearable="clearable"
                                  :disabled="!formModel.city"
                                  @on-change="changedistrict" placeholder="">
                            <i-option v-for="(item,index) in districtList" :value="item.dictCode" :key="item.dictCode">
                                {{item.dictName}}
                            </i-option>
                        </i-select>
                    </Form-item>
                </i-col>
            </Row>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 
     * @Props: [value: Object] v-model
     * @Props: [clearable: Boolean]
     * @Props: [type: Stirng]123
     */
    import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

    export default {
        name: 'regionPicker',
        props: {
            value: {
                default: {
                    "province": "", //
                    "city": "", //
                    "district": "", ///
                    "province_name": "", //
                    "city_name": "", //
                    "district_name": "", ///
                }
            },
            type: {
                default: '3'
            },
            clearable: {
                default: false
            },
        },
        data() {
            let regionRule = {
                province: [{required: true, message: '', trigger: 'change'}],
                city: [{required: true, message: '', trigger: 'change'}],
                district: [{required: true, message: '', trigger: 'change'}],
            }
            return {
                regionRule: regionRule,
                dateGroup: [],
                formModel: undefined,
                selectedProvince: {},
                selectedCity: {},
                selectedDistrict: {},
                provinceList: [],//
                cityList: [],
                districtList: [],
                inited: false
            }
        },
        computed: {
            ...mapState({
                'dp': state => state.dict.dict
            }),
            showProvince() {
                return this.formModel && this.inited && true;
            },
            showCity() {
                return this.formModel && parseInt(this.type) > 1;
            },
            showDistrict() {
                return this.formModel && parseInt(this.type) > 2;
            },
            info() {
                return {
                    province: {
                        ...this.selectedProvince,
                        value: this.formModel.province,
                        name: this.formModel.province_name,
                    },
                    city: {
                        ...this.selectedCity,
                        value: this.formModel.city,
                        name: this.formModel.city_name
                    },
                    district: {
                        ...this.selectedDistrict,
                        value: this.formModel.district,
                        name: this.formModel.district_name
                    },
                }
            }
        },
        created() {
            //
            this.formModel = {
                ...
                    this.value
            };
            //
            if (this.formModel.province && this.showCity) {
                this.getRegion('2', this.formModel.province, true)
            }
            //
            if (this.formModel.city && this.showDistrict) {
                this.getRegion('3', this.formModel.city, true)
            }
            this.inited = true;

        },
        mounted() {
        },
        methods: {
            ...mapMutations('app', ['showLoading', 'hideLoading']),
            ...mapActions('dict', ['queryDictData']),
            //id
            getRow(level, val) {
                let arr = [];
                if (level == '1') arr = this.dp['province'];
                if (level == '2') arr = this.cityList;
                if (level == '3') arr = this.districtList;
                let obj = arr.find(item => item.dictCode == val);
                return obj ? obj : null;
            },
            //,$params: level--pid--id  
            getRegion(level, pid, isinit) {
                level = level || '1';
                this.$http.get(baseserver + '/dictionary/getNextDictionaryList' + '/AR:' + pid).then((res) => {
                    let result = res.data;
                    if (!result.success) return this.$Message.error(result.errorMessage || '' + pid + '');
                    if (level == '1') {
                        this.provinceList = result.data || [];
                        this.selectedProvince = this.getRow('1', this.formModel.province);
                        if (!isinit) this.model();
                    }
                    if (level == '2') {
                        this.cityList = result.data || [];
                        if (!this.cityList.length) this.$Message.error((this.selectedProvince.dictName || '') + '');
                        this.selectedCity = this.getRow('2', this.formModel.city);
                        if (!isinit) this.model();
                    }
                    if (level == '3') {
                        this.districtList = result.data || [];
                        if (!this.districtList.length) {
                            let city = this.getRow('2', this.formModel.city);
                            this.$Message.error((city ? city.dictName : '') + '/');
                        }
                        this.selectedDistrict = this.getRow('3', this.formModel.district);
                        if (!isinit) this.model();
                    }
                }).catch((err) => {
                    this.errorCatchModal('' + pid + '', err);
                })
            },
            //,$params: obj--iviewlabelvalue
            changeProvince(value) {
                let row = this.getRow('1', value);
                this.selectedProvince = row;
                this.formModel.province_name = row ? row.dictName : '';
                this.formModel.city = '';
                this.formModel.city_name = '';
                this.formModel.district = '';
                this.formModel.district_name = '';
                if (!value) {
                    this.cityList = [];
                    this.districtList = [];
                } else if (this.showCity) this.getRegion('2', value);
                if (this.type == '1') this.model();
            },
            // 
            changeCity(value) {
                let row = this.getRow('2', value);
                this.selectedCity = row;
                this.formModel.city_name = row ? row.dictName : '';
                this.formModel.district = '';
                this.formModel.district_name = '';
                if (!value) {
                    this.districtList = [];
                } else if (this.showDistrict) this.getRegion('3', value);
                if (this.type == '2') this.model();
            },
            /// 
            changedistrict(value) {
                let row = this.getRow('3', value);
                this.selectedDistrict = row;
                this.formModel.district_name = row ? row.dictName : '';
                this.model();
            },
            //
            model() {
                this.$emit('input', this.formModel);
                this.$emit('on-change');
            },
            //
            validate(cb) {
                let flag = true;
                this.$refs['formModel'].validate((valid) => {
                    flag = flag && valid;
                    cb && cb(flag);
                });
                return flag;
            },
        },
    }
</script>



